import React from "react";
import Style from './MyPosts.module.scss'
import Post from "./Post/Post";
import { Form, Field } from 'react-final-form'
import { required } from "../../../utils/validators/validators";
import { Textarea } from "../../common/FormsControls/FormsControls";


function PutPostForm(props) {
    return (
        <Form onSubmit={props.onSubmit} render={({ handleSubmit, reset, submitting, pristine }) => (
            <form onSubmit={handleSubmit} className={Style.formArea} >
                <Field name="message" component={Textarea} validate={required} />
                <button disabled={submitting || pristine} >Add post</button>
            </form>
        )}
        />
    );
}

const MyPosts = (props) => {
    let postsElements = props.posts.map((item) => <Post message={item.message} likes={item.likesCount} key={item.id} sender={item.sender} changeLikeCount={props.changeLikeCount} id={item.id} iLike={item.iLike} />);

    const handleClick = (values) => {
        props.addPost(values.message);
    };
    return (
        <div className={Style.myposts}>
            <h2 className={Style.hStyle}>My posts</h2>
            <PutPostForm onSubmit={handleClick} />
            <div>
                <h2 className={Style.hStyle}>Posts</h2>
                {postsElements}
            </div>
        </div>
    );
}
export default React.memo(MyPosts);