import React from "react";
import { connect } from "react-redux";
import Profile from "./Profile";
import { setUserProfileAC, getCurrentProfileTC, getUserStatusTC, updateUserStatusTC, savePhotoTC, updateProfileTC } from '../../redux/profile-reducer'
import { useParams } from "react-router-dom";
import withAuthRedirect from '../../hoc/withAuthRedirect';
import { compose } from "redux";


const withRouter = WrappedComponent => props => {
  let params = useParams();
  // etc... other react-router-dom v6 hooks
  return (
    <WrappedComponent
      {...props}
      params={params}
    // etc...
    />
  );
};

class ProfileContainer extends React.Component {

  refreshProfile = () => {
    let userId = this.props.params.userId;
    if (!userId) {
      if (this.props.isAuth) {
        userId = this.props.logId;
      }
    }
    this.props.getCurrentProfileTC(userId);
    this.props.getUserStatusTC(userId);
    /* profileAPI.getCurrentProfile(userId).then(response => {
       this.props.setUserProfile(response.data);
     })*/
  }

  componentDidMount() {
    this.refreshProfile();
  }
  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevProps.params.userId != this.props.params.userId) {
      this.refreshProfile();
    }
  }
  render() {
    return (
      <Profile {...this.props} profile={this.props.profile}
        status={this.props.status}
        updateStatus={this.props.updateUserStatusTC}
        isOwner={!!this.props.params.userId}
        savePhoto={this.props.savePhotoTC}
        updateProfileTC={this.props.updateProfileTC}
        userId={this.props.logId}
        icons={this.props.icons} />
    );
  }
}


let mapStateToProps = (state) => ({
  profile: state.profilePage.profile,
  status: state.profilePage.status,
  logId: state.auth.id,
  isAuth: state.auth.isAuth,
  icons:state.profilePage.icons
});

export default compose(
  connect(mapStateToProps, {
    setUserProfile: setUserProfileAC,
    getCurrentProfileTC,
    getUserStatusTC,
    updateUserStatusTC,
    savePhotoTC,
    updateProfileTC,
  }),
  withRouter,
  withAuthRedirect
)(ProfileContainer);
